import { useState, useEffect } from "react";
import { addresses, TOKEN_DECIMALS } from "../../constants";
import { Link, SvgIcon, Popper, Button, Paper, Typography, Divider, Box, Fade, Slide } from "@material-ui/core";
import { ReactComponent as InfoIcon } from "../../assets/icons/info-fill.svg";
import { ReactComponent as ArrowUpIcon } from "../../assets/icons/arrow-up.svg";
import { ReactComponent as ssapTokenImg } from "../../assets/tokens/SSAP.svg";
import { ReactComponent as sapTokenImg } from "../../assets/tokens/SAP.svg";

import "./sapmenu.scss";
import { busd } from "src/helpers/AllBonds";
import { useWeb3Context } from "../../hooks/web3Context";

import SapImg from "src/assets/tokens/SAP.svg";
import SSapImg from "src/assets/tokens/SSAP.svg";

const addTokenToWallet = (tokenSymbol, tokenAddress) => async () => {
  if (window.ethereum) {
    const host = window.location.origin;
    // NOTE (appleseed): 33T token defaults to sSAP logo since we don't have a 33T logo yet
    let tokenPath;
    // if (tokenSymbol === "SAP") {

    // } ? SapImg : SSapImg;
    switch (tokenSymbol) {
      case "SAP":
        tokenPath = SapImg;
        break;
      default:
        tokenPath = SSapImg;
    }
    const imageURL = `${host}/${tokenPath}`;

    try {
      await window.ethereum.request({
        method: "wallet_watchAsset",
        params: {
          type: "ERC20",
          options: {
            address: tokenAddress,
            symbol: tokenSymbol,
            decimals: TOKEN_DECIMALS,
            image: imageURL,
          },
        },
      });
    } catch (error) {
      console.log(error);
    }
  }
};

function SapMenu() {
  const [anchorEl, setAnchorEl] = useState(null);
  const isEthereumAPIAvailable = window.ethereum;
  const { chainID } = useWeb3Context();

  const networkID = chainID;

  const SSAP_ADDRESS = addresses[networkID].SSAP_ADDRESS;
  const SAP_ADDRESS = addresses[networkID].SAP_ADDRESS;

  const handleClick = event => {
    setAnchorEl(anchorEl ? null : event.currentTarget);
  };

  const open = Boolean(anchorEl);
  const id = "sap-popper";
  const busdAddress = busd.getAddressForReserve(networkID);
  return (
    <Box
      component="div"
      onMouseEnter={e => handleClick(e)}
      onMouseLeave={e => handleClick(e)}
      id="sap-menu-button-hover"
    >
      <Button id="sap-menu-button" size="large" variant="contained" color="secondary" title="SAP" aria-describedby={id}>
        <SvgIcon component={InfoIcon} color="primary" />
        <Typography>SAP</Typography>
      </Button>

      <Popper id={id} open={open} anchorEl={anchorEl} placement="bottom-start" transition>
        {({ TransitionProps }) => {
          return (
            <Fade {...TransitionProps} timeout={100}>
              <Paper className="sap-menu" elevation={1}>
                <Box component="div" className="buy-tokens">
                  <Link
                    href={`https://sapphireswap.finance/swap?inputCurrency=${busdAddress}&outputCurrency=${SAP_ADDRESS}`}
                    target="_blank"
                    rel="noreferrer"
                  >
                    <Button size="large" variant="contained" color="secondary" fullWidth>
                      <Typography align="left">
                        Buy on SapphireSwap <SvgIcon component={ArrowUpIcon} htmlColor="#A3A3A3" />
                      </Typography>
                    </Button>
                  </Link>
                </Box>

                {isEthereumAPIAvailable ? (
                  <Box className="add-tokens">
                    <Divider color="secondary" />
                    <p>ADD TOKEN TO WALLET</p>
                    <Box display="flex" flexDirection="row" justifyContent="space-between">
                      <Button variant="contained" color="secondary" onClick={addTokenToWallet("SAP", SAP_ADDRESS)}>
                        <SvgIcon
                          component={sapTokenImg}
                          viewBox="0 0 52 52"
                          style={{ height: "25px", width: "25px" }}
                        />
                        <Typography variant="body1">SAP</Typography>
                      </Button>
                      <Button variant="contained" color="secondary" onClick={addTokenToWallet("sSAP", SSAP_ADDRESS)}>
                        <SvgIcon
                          component={ssapTokenImg}
                          viewBox="0 0 52 52"
                          style={{ height: "25px", width: "25px" }}
                        />
                        <Typography variant="body1">sSAP</Typography>
                      </Button>
                    </Box>
                  </Box>
                ) : null}
              </Paper>
            </Fade>
          );
        }}
      </Popper>
    </Box>
  );
}

export default SapMenu;
